body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

#about_container{margin:20vh 0}#about_container #main_heading_container{display:grid;place-items:center}#about_container #main_heading_container #main_heading{color:#444649;font-family:"Raleway",sans-serif;font-size:25pt;position:relative;position:relative;text-transform:uppercase;font-weight:bolder}#about_container #main_heading_container #main_heading_bar{background:#0d3065;height:4px;margin:25px 0 75px 0;width:70px}#about_container #design_container{margin:0 10vw 0 15vw;display:grid;-webkit-justify-content:space-around;justify-content:space-around;grid-gap:6%;grid-template-columns:repeat(auto-fill, minmax(50px, 180px))}#about_container #design_container #design-polygon{height:101px;width:clamp(120px, 120px, 120px);background-color:#04c2c9;-webkit-clip-path:polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);clip-path:polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%)}#about_container #design_container #design-polygon #design-logo{color:#fff;font-size:42pt;margin:0px 10px 0px 30px;padding-top:17px}#about_container #design_container #design-content{margin:2vh 0px 0px 25px}#about_container #design_container #design-content #design-title{color:#616161;font-size:16pt;font-weight:bolder;font-family:"Raleway",sans-serif}#about_container #design_container #design-content #design-description{padding:.5vh 0 0 0;color:#616161;line-height:12pt;font-family:"Raleway",sans-serif;font-size:11pt;font-weight:500}#about_container #profile_section{margin:16vh 16vw 0 15vw;display:grid;-webkit-justify-content:space-around;justify-content:space-around;grid-gap:6%;grid-template-columns:repeat(auto-fill, minmax(180px, 400px))}#about_container #profile_section #profile-container #profile-image{margin:0 3vw;-webkit-clip-path:polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);clip-path:polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);height:40vh;background-color:#04c2c9}#about_container #profile_section #profile-container #profile-introduction{margin:5vh 0px 0px 25px}#about_container #profile_section #profile-container #profile-introduction #who-this{color:#616161;font-family:"Raleway",sans-serif;line-height:18pt;text-align:center;font-size:25px;font-weight:bolder}#about_container #profile_section #profile-container #profile-introduction #who-this-description{color:#616161;line-height:18pt;padding:15px 0;font-family:"Raleway",sans-serif;text-align:justify}@media(max-width: 550px){#about_container #profile_section #profile-container #profile-image{height:240px}#about_container #profile_section #profile-container #who-this{font-size:11.5pt}#about_container #profile_section #profile-container #who-this-description{font-size:10.5pt}}@media(max-width: 470px){#about_container #profile_section #profile-container #profile-image{height:27vh}}#about_container #profile_section #skills-section{margin:0;display:-webkit-flex;display:flex;height:450px;width:clamp(100%, 120%, 120%);-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-content:center;align-content:center}#about_container #profile_section #skills-section #skill-bar{background-color:#eee;padding:3px 13px 0 0;-webkit-flex:auto;flex:auto;height:30px;width:auto;color:#000;text-align:right}#about_container #profile_section #skills-section #skill-width{height:30px;background-color:#00a1a7}#about_container #profile_section #skills-section #skill-name{padding-top:5px;background-color:#04c2c9;font-family:"Raleway",sans-serif;font-weight:bolder;color:#fff;width:clamp(22%, 27%, 27%);height:30px;font-size:11pt;text-align:center}@media(max-width: 500px){#about_container #profile_section #skills-section #skill-name{font-size:9pt;padding:7px}}@media(max-width: 900px){#about_container #profile_section #skills-section{margin-left:-4.5vw}}@media(max-width: 533px){#about_container #design_container{margin:0 20px;grid-gap:10%;-webkit-justify-content:center;justify-content:center;grid-template-columns:repeat(auto-fill, minmax(20px, 150px))}#about_container #design_container #design-content #design-title{font-size:14pt}#about_container #design_container #design-content #design-description{font-size:10pt}}@media(max-width: 399px){#about_container #design_container{margin:0 5px;grid-gap:9%;-webkit-justify-content:center;justify-content:center;grid-template-columns:repeat(auto-fill, minmax(20px, 120px))}#about_container #design_container #design-content #design-title{font-size:13pt}#about_container #design_container #design-content #design-description{font-size:10pt}}
.header{display:-webkit-flex;display:flex;background-color:#1b242f;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:0px 55px;height:60px;font-family:"Raleway",sans-serif;color:#fff;text-decoration:none;border-bottom:4px solid #04c2c9}.header .logo-nav{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly}.header .logo-container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.header .logo{width:40px;height:40px}.header .nav_options{padding-left:55px;display:grid;grid-template-columns:repeat(5, auto);grid-gap:50px;list-style-type:none;cursor:pointer;font-weight:bolder;font-size:1rem}.header .mobile-option{display:none;transition:.2s}.header .option :hover{color:#fff;transition:.2s}.header .option :hover:hover{color:#e31b6d}.header .mobile_menu{display:none}@media(max-width: 730px){.header .header{padding:0px 10px}.header .logo{width:45px;height:40px}.header .nav_options{margin:0;display:-webkit-flex;display:flex;width:100%;height:auto;position:absolute;top:60px;left:-100%;opacity:0;transition:all .5s ease;-webkit-flex-direction:column;flex-direction:column;list-style-type:none;grid-gap:0px}.header .nav_options.active{background-color:#666;left:0;opacity:1;transition:all .5s ease;z-index:1;-webkit-align-content:center;align-content:center;padding-left:0px}.header .menu_icon{width:45px;height:45px}.header .option{border-bottom:1px solid #fff;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;height:10vw;padding:30px 0px}.header .mobile_menu{display:block}}
body{overflow-x:hidden}body img{width:500px}body #particles-js{background:radial-gradient(circle, #252a34 0%, #252a34);height:100vh}body #welcome{position:absolute;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;width:100vw;height:100vh;font-family:"Raleway",sans-serif;letter-spacing:.01cm;font-size:3vw;font-weight:bolder;color:#fff}body #welcome .scale_in_center{-webkit-animation:scale_in_center 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 1s both;animation:scale_in_center 1.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) 1s both}@-webkit-keyframes scale_in_center{0%{-webkit-transform:scale(0);transform:scale(0);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}@keyframes scale_in_center{0%{-webkit-transform:scale(0);transform:scale(0);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:1}}body #welcome #view_work_btn{border:2px solid #fff;font-family:"Raleway",sans-serif;letter-spacing:.04cm;background:transparent;transition:.2s;font-size:12pt;outline:none;letter-spacing:.02cm;padding:14pt 10pt;color:#fff}body #welcome #view_work_btn:hover{background-color:#04c2c9;cursor:pointer}body #welcome #view_work_btn #workbtn_logo{padding-top:13vw}@media(max-width: 559px){body #welcome #view_work_btn{padding:2vh 4vw}}
